declare var swal: any;
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { CompanyService } from 'src/app/service/company.service';
import { DataService } from 'src/app/service/data.service';
import { CompanyAddComponent } from '../company-add/company-add.component';
import { PaginationService } from 'src/app/service/pagination.service';
import { TextClickEventField, ActionField, TextField } from '../../../common-module/table-list/list-field-type';


@Component({
  selector: 'app-company-list',
  templateUrl: './company-list.component.html'
})
export class CompanyListComponent implements OnInit {
  action: any;
  loadform = false;
  loadingState = true;
  objectArray: Array<any> = [];
  pagination: any = null;
  searchText: any = null;
  currentPage: any = 1;
  permissionObject: any = null;

  PageTitle = "Company";

  //For Dynamic List
  headerData: Array<any> = [];

  constructor(
    private router: Router,
    private paginationService: PaginationService,
    private companyService: CompanyService,
    private dataService: DataService
  ) { }

  ngOnInit() {
    // this.dataService.permission.subscribe((perms) => {
    //   this.permissionObject = perms['company'];
    // });
    this.loadform = false;
    this.setHeaderData();
    this.getObjects();
  }
  setHeaderData() {
    this.headerData.push(
      new TextClickEventField({
        label: "Company Name",
        key: "company_name",
        viewUrl: {
          path: '/company/edit/',
          id: 'company_id'
        }
      }),
      new TextField({
        label: "Company Email",
        key: "company_email",
      }),
    );
    let canEdit = true;
    let canDelete = false;
    let canView =true;
    let actions = {};
    if (canEdit) {
      actions['edit'] = {
        path: '/company/edit/',
        id: 'company_id'
      }
    }
    if (canDelete) {
      actions['delete'] = {
        path: 'company/',
        id: 'company_id'
      }
    }
    if (canView) {
      actions['view'] = {
        path: '/company/view/',
        id: 'company_id'
      }
    }
    let actionColom = new ActionField(actions);
    this.headerData.push(
      actionColom
    );
  }

  getObjects() {
    const params: any = { page: this.currentPage };
    if (this.searchText) {
      params.search = this.searchText;
    }
    this.companyService.getCompanyList(params).subscribe((response) => {
      this.loadingState = false;
      if (response.success) {

        this.objectArray = response.data.list;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;
    });
  }


  getPage(page: number) {
    this.currentPage = page;
    this.getObjects();
  }

  searchObject(text) {
    this.searchText = text;
    this.currentPage = 1;
    this.getObjects();
  }



  onReloadEvent() {
    this.getObjects();
  }

  onClickEvent(obj) {
    this.onCompanySelect(obj);
  }

  onCompanySelect(obj) {
    this.companyService.selectCompany(obj.company_id).subscribe((response) => {
      if (response.success) {
        this.dataService.saveToken(response.data.Token['token']);
        this.dataService.updateCompany(response.data.Company);
        this.router.navigateByUrl('/dashboard');
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    });
  }

}




