import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CompanyListComponent } from './company-list.component';
import { Routes, RouterModule } from '@angular/router';
import { TableViewModule } from 'src/app/common-module/table-view/table-view.module';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { TableListModule } from '../../../common-module/table-list/table-list.module';

const routes: Routes = [
  {
    path: '',
    component: CompanyListComponent,
    data: { title: 'company_list' }
  },
  {
    path: 'add',
    loadChildren: () => import('../company-add/company-add.module').then(m => m.CompanyAddModule),
    data: { title: 'add_company' }
  },
  {
    path: 'edit/:id',
    loadChildren: () => import('../company-add/company-add.module').then(m => m.CompanyAddModule),
    data: { title: 'edit_company' }
  },
  {
    path: 'view/:id',
    loadChildren: () => import('../company-view/company-view.module').then(m => m.CompanyViewModule),
    data: { title: 'company_list' }
  },
 
];

@NgModule({
  declarations: [CompanyListComponent],
  imports: [
    CommonModule,
    SearchModule,
    BtnAddModule,
    TableViewModule,
    TableListModule,
    RouterModule.forChild(routes)
  ]
})
export class CompanyListModule { }
